<template>
	<div class="gov-area-panel" v-loading="mainLoading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<area-panel area-title="经营规模统计" @hand-sub-title="handlePage">
			<div slot="panelMain" class="module-body">
				<div class="echart-container" ref="EContainer" v-show="hasData"></div>
				<div class="no-data" v-show="!hasData"><span>暂无数据</span></div>
			</div>
		</area-panel>
	</div>
</template>

<script>
import areaPanel from '@/components/overview/area-panel';
import {areaLevelCorAreaId, maxDecimalPlaces} from '@/common/fn';
import {getStorage} from '@/utils/storage';

const subVo = getStorage('govGovernmentSubjectVO');
// echarts 超级变量含义 {a} series.name, {b} data.name {c} data.value {d} data%
const barOption = {
	grid: {
		show: false,
		height: '85%',
		width: '86%',
		left: '7%',
		bottom: '15%'
	},
	tooltip: {
		show: true,
		trigger: 'item',
		formatter(a) {
			return `${a.name}：${a.value}家`;
		},
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		}
	},
	xAxis: {
		data: [],
		axisLabel: {
			interval: 0,
			textStyle: {
				color: '#6EC2CB',
				fontSize: 12,
				lineHeight: 14
			},
			align: 'center'
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: false
		}
	},
	yAxis: {
		show: false,
		name: '单位：',
		nameTextStyle: {
			fontSize: 14,
			color: '#6DC1CB',
			padding: [0, 0, 10, 0]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	series: [
		{
			// 背景bar
			type: 'bar',
			itemStyle: {
				normal: {
					color: 'rgba(4, 190, 254, 0.2)'
				}
			},
			silent: true,
			barWidth: 50,
			barGap: '-100%', // Make series be ove
			data: []
		},
		{
			type: 'bar',
			barWidth: 50,
			data: [],
			label: {
				show: true,
				position: 'top',
				// color: '#030E22',
				fontSize: 12,
				formatter: '{c}家'
			},
			z: 10,
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#00F6FF' // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#01C1FF' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			}
		}
	]
};
export default {
	name: 'business-scale-4',
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			mainLoading: true,
			chartInstance: null,
			hasData: true
		};
	},
	mounted() {
		this.$nextTick(() => {
			this.initMoudle();
		});
	},
	methods: {
		async initMoudle() {
			this.hasData = this.mainLoading = true;
			this.chartInstance = this._initEcharts(this.eContainerRef, barOption);
			let [result, [postfarmAreaId, postfarmAreaType]] = await this.getBusinessScale(this.farmAreaInfo.farmAreaId, this.farmAreaInfo.farmAreaType);
			if (!result) return;
			if (postfarmAreaId !== this.farmAreaInfo.farmAreaId || postfarmAreaType !== this.farmAreaInfo.farmAreaType) return;
			const res = result.reverse();
			const totalValue = res.reduce((total, item) => {
				return total + item.count;
			}, 0);
			if (totalValue === 0) {
				this.hasData = false;
				this.mainLoading = false;
				return;
			}
			this.refreshChart(this.chartInstance, res, totalValue);
			this.mainLoading = false;
		},
		refreshRender() {
			if (this.chartInstance) {
				this.chartInstance.resize();
			}
		},
		// 获取经营主体规模分布
		getBusinessScale(farmAreaId, farmAreaType) {
			var args = arguments;
			return this.$ajax
				.post(this.$api.overview.getBusinessScale, {
					farmAreaId: [farmAreaId],
					farmAreaType: farmAreaType,
					dataTargetFlag: subVo.dataTargetFlag
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						return [data.obj, [...args]];
					} else {
						console.log(new Error('获取经营主体规模分布error'), e);
						return [null, [...args]];
					}
				})
				.catch(e => {
					console.log(new Error('获取经营主体规模分布error'), e);
				});
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		async refreshChart(chartInstance, res, totalValue) {
			if (res && res.length && res.length > 1) {
				const chartOpts = {
					xAxis: {
						data: res.map(item => `${item.name}亩`)
					},
					series: [
						{
							data: new Array(res.length).fill(totalValue)
						},
						{
							data: res.map(item => {
								// 当前值的占比
								const curValuePercentage = maxDecimalPlaces({
									value: item.count / totalValue
								});
								const maxPercentage = 0.84;
								// 超出
								const exceedMaxPercentage = curValuePercentage > maxPercentage;
								return {
									value: item.count,
									label: {
										position: exceedMaxPercentage ? 'insideTop' : 'top',
										color: exceedMaxPercentage ? '#033250' : '#00F6FF'
									}
								};
							})
						}
					]
				};
				chartInstance.setOption(chartOpts);
			}
		},
		handlePage() {
			// this.$router.push({
			// 	name: 'operateSubjectList'
			// });
			this.$emit('handleClick', 'operateSubjectList', '经营规模统计');
		}
	},
	computed: {
		eContainerRef() {
			return this.$refs.EContainer;
		}
	},
	components: {
		areaPanel
	}
};
</script>

<style scoped lang="less">
.gov-area-panel {
	width: 100%;
	height: 100%;
	background-color: fade(#061f36, 34%);
	.module-body {
		width: 100%;
		height: 100%;
		.echart-container {
			height: 100%;
		}
		.no-data {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 14px;
			color: #00f6ff;
			font-weight: bold;
		}
	}
	/deep/ .el-loading-spinner {
		overflow: hidden;
		.iconfont {
			display: block;
			font-size: 40px;
			color: #00f6ff;
			animation: loading 1.25s linear infinite;
			margin-top: 24px;
			margin-left: 8px;
		}
	}
}
</style>



